.container {
  width: 800px;
  min-height: 300px;
  padding: 40px;
  background: #fff;
  border-radius: 2px;
  box-shadow: 0 1px 2px rgba(0,0,0,.2);
  position: relative;
  margin:auto;
}

.Tab {
  border-bottom: 3px solid #47c9af;
  display: flex;
}

.Tabs {
  width: 100px;
  height: 20px;
  text-align: center;
  padding: 5px;
  cursor: pointer;
}

.Content {
  margin-top: 40px;
}

.active { 
  background: #47c9af;
  color: white;
  font-weight: 600;
  border-radius: 5px 5px 0 0;
}